const tableColumns = [
  {
    title: 'Title',
    field: 'title',
    isIcon: false,
    icon: ''
  },
  {
    title: 'Created At',
    field: 'created_at',
    isIcon: false,
    icon: ''
  }
]

const tableActions = [
  {
    tag: 'link',
    class: 'btn btn-info',
    label: 'Edit',
    dynamic: false,
    to: '/categories/update',
    urlType: 'category_id',
    emit: ''
  },
  {
    tag: 'button',
    class: 'btn btn-danger',
    label: 'Remove',
    dynamic: false,
    urlType: 'category_id',
    to: '',
    emit: 'clickDelete'
  }
]

const fields = [
  {
    name: 'title',
    label: 'Category title',
    placeholder: 'Category title',
    onChange: 'handleSetCategory',
    value: 'title',
    type: 'fg-input'
  },
  {
    name: 'title_cn',
    label: 'Chinese title',
    placeholder: 'Chinese title',
    onChange: 'handleSetCategory',
    value: 'title_cn',
    type: 'fg-input'
  },
  {
    name: 'type',
    label: 'Category type',
    placeholder: 'Category type',
    onChange: 'handleSetCategory',
    value: 'type',
    type: 'fg-select',
    selected: 'location',
    options: [
      {
        _id: 'location',
        value: 'location',
        name: 'Location'
      },
      {
        _id: 'event',
        value: 'event',
        name: 'Event'
      },
      {
        _id: 'highlight',
        value: 'highlight',
        name: 'Highlight'
      }
    ]
  }
]

export {
  tableColumns,
  tableActions,
  fields
}



