<template>
  <div class="row">
    <div class="col-md-8 col-md-offset-2">
      <div class="card">
        <div class="header">
          <h4 class="title">Adding Event</h4>
        </div>
        <div class="content">
          <form>
            
            <ImageUplodaer
              title="Add image"
              uploading-text="Uploading..."
              @uploadPoster="val => handleSetCategory(val, 'poster')"
              :poster="form.poster"
            />
            <component
              type="text"
              v-for="({
                label,
                value,
                subValue,
                name,
                placeholder,
                showedKey,
                type,
                options,
                selected
              }, key) in fields"
              :selected="form[value]"
              :options="options"
              :is="type"
              :key="key"
              :label="label"
              :placeholder="placeholder"
              @changeValue="handleSetCategory"
              :name="name"
              :value="form[value]"
            />
            <div class="form-group" v-if="form.type === 'highlight'">
              <label>
                Pick Color
              </label>
              <div class="form-item">
                <el-color-picker
                  @change="onColorPick"
                  :value="form.color"
                />
                <span class="color-item">{{form.color}}</span>
              </div>
            </div>
            
            <fg-textarea 
              label="Category description"
              @changeValue="handleSetCategory"
              name="description"
              :value="form.description"
              placeholder="Description of event"
            />
            <div class="text-center">
              <button 
                type="submit"
                class="btn btn-info btn-fill btn-wd" 
                @click.prevent="submitForm"
              >
                {{id ? 'Update' : 'Create'}}
              </button>
              <router-link 
                to="/categories/list" 
                class="btn btn-danger btn-fill btn-wd"
              >
                Back
              </router-link>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from 'vuex'
import { types, fields } from './TableMock.js'

export default {
  data: () => ({
    id: '',
    types,
    fields,
    subedKey: 'location'
  }),
  components: {
    ImageUplodaer: () => import('ui/ImageUploaderPlugin/ImageUploader.vue')
  },
  mounted() {
    this.id = this.$route.params.id
    if (this.id) {
      this.fetchCategory({ id: this.id })
    }
  },
  watch: {
    form() {
      console.log('changed')
    }
  },
  computed: {
    ...mapState({
      form: state => state.categories.category
    })
  },
  methods: {
    submitForm () {
      const id = this.id ? this.id : ''
      id ? this.updateCategory({ id }) : this.createCategory()
      this.$router.push('/categories/list')
    },
    ...mapActions({
      fetchCategory: 'fetchCategory',
      createCategory: 'createCategory',
      updateCategory: 'updateCategory'
    }),
    ...mapMutations({
      resetForm: 'resetCategory',
      setPoster: 'setPoster',
      updateCategoryForm: 'updateCategoryForm'
    }),
    onColorPick(value) {
      this.updateCategoryForm({
        key: 'color',
        value
      })
    },
    handleSetCategory(value, key) {
      const payload = {
        key,
        value
      }
      this.updateCategoryForm(payload)
    }
  }
}
</script>

<style lang="stylus">
  .form-item
    display flex
    align-items center

    .color-item
      margin-left 10px
</style>



